        -ms-flex-pack: center;
            justify-content: center;
    }

.Button--align-right .Button__in {
    -webkit-box-pack: end;
    -webkit-justify-content: flex-end;
        -ms-flex-pack: end;
            justify-content: flex-end;
    }

.Button--type-cell {
  display: block;
  width: 100%;
  border: none;
  background: transparent;
  }

.Button--type-cell.Tappable--active {
  position: relative;
  z-index: 2;
  }

.Button--type-cell.Button--level-primary {
  color: #528bcc;
  }

.Button--type-cell.Button--level-danger {
  color: #e64646;
  }

.Button--stretched {
  display: block;
  width: 100%;
  }

.Button--stretched .Button__in {
    -webkit-box-pack: center;
    -webkit-justify-content: center;
        -ms-flex-pack: center;
            justify-content: center;
    }

.Button--type-default {
  border: 1px solid transparent;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
      -ms-flex-pack: center;
          justify-content: center;
  }

.Button--type-default .Button__before .Icon--16 {
      margin-right: 6px;
      }

.Button--type-default .Button__before .Icon--24 {
      margin-right: 8px;
      }

.Button--type-default::before,
  .Button--type-default::after {
    display: none;
    }

.Button--level-1 {
  background-color: #5181b8;
  color: #fff;
  }

.Button--level-1.Tappable--active:not([disabled]) {
  background-color: #4872a3;
  }

.Button--level-2 {
  background-color: rgba(0, 54, 115, .1);
  color: #45678f;
  }

.Button--level-2.Tappable--active:not([disabled]) {
  background-color: rgba(0, 54, 115, .2);
  }

.Button--level-3 {
  background-color: transparent;
  color: #5181b8;
  }

.Button--level-3.Tappable--active:not([disabled]) {
  background-color: rgba(0, 54, 115, .08);
  }

.Button--level-buy {
  background-color: #4bb34b;
  color: #fff;
  }

.Button--level-buy.Tappable--active {
  background-color: rgb(66, 158, 66);
  }

.Button--level-sell {
  background-color: transparent;
  border: 1px solid #528bcc;
  color: #528bcc;
  }

.Button--level-sell.Tappable--active:not([disabled]) {
  background-color: rgba(82, 139, 204, .1);
  }

.Button--size-m {
  padding: 0 16px;
  }

.Button--size-m .Button__before {
    margin-left: -4px;
    }

.Button--size-m .Button__content {
    font-size: 14px;
    line-height: 14px;
    font-weight: 500;
    padding: 7px 0;
    }

.Button--size-l {
  padding: 0 16px;
  }

.Button--size-l .Button__before {
    margin-left: -4px;
    }

.Button--size-l .Button__content {
    font-size: 15px;
    font-weight: 500;
    padding: 8px 0;
    }

.Button--size-xl {
  width: 100%;
  padding: 0 16px;
  }

.Button--size-xl .Button__before {
    margin-left: -6px;
    margin-right: 2px;
    }

.Button--size-xl .Button__before ~ .Button__content {
    padding-left: 0;
    }

.Button--size-xl .Button__in {
    -webkit-box-pack: center;
    -webkit-justify-content: center;
        -ms-flex-pack: center;
            justify-content: center;
    }

.Button--size-xl .Button__content {
