  }

.Button--ios.Button--type-cell .Button__content {
    line-height: 20px;
    font-size: 17px;
    padding: 12px 0;
    }

.Button--ios.Button--type-cell .Button__before .Icon {
      margin-right: 12px;
      }

/*
  Android
 */

.Button--android {
  font-family: Roboto, Arial;
  }

