/**
 * iOS version
 */

.Alert--ios {
  width: 270px;
  -webkit-box-flex: 0;
  -webkit-flex: 0 0 auto;
      -ms-flex: 0 0 auto;
          flex: 0 0 auto;
  background: #fafafa;
  border-radius: 12px;
  -webkit-animation: animation-ios-alert-intro .3s cubic-bezier(.36, .66, .04, 1);
          animation: animation-ios-alert-intro .3s cubic-bezier(.36, .66, .04, 1);
  }

/**
   * Content container
   */

.Alert--ios .Alert__content {
    position: relative;
    padding: 19px 16px 20px;
    font-size: 13px;
    line-height: 16px;
    text-align: center;
    }

.Alert--ios .Alert__content::after {
    content: '';
    position: absolute;
    bottom: 0;
    height: 1px;
    right: 0;
    left: 0;
    background: #e0e0e0;
    }

.Alert--ios .Alert__content h2 {
      margin: 0 0 7px;
      font-weight: bold;
      font-size: 17px;
      line-height: 22px;
      }

.Alert--ios .Alert__content p {
      margin: 0;
      }

/**
   * Footer
   */

.Alert__footer {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    max-width: 100%;
    }

.Alert--ios.Alert--v .Alert__footer {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
        -ms-flex-direction: column;
            flex-direction: column;
    }

/**
     * iOS Alert button
     */

.Alert--ios .Alert__btn {
      position: relative;
      background: #fafafa;
      font-size: 17px;
      line-height: 44px;
      color: #528bcc;
      border: 0;
      padding: 0 6px;
      margin: 0;
      height: 44px;
      display: block;
      outline: none;
      overflow: hidden;
      -o-text-overflow: ellipsis;
         text-overflow: ellipsis;
      font-family: -apple-system, Helvetica Neue, Arial;
      }

.Alert--ios .Alert__btn:active {
      z-index: 1;
      }

.Alert--ios .Alert__btn::after {
      content: '';
      position: absolute;
      background: rgba(0, 0, 0, .12);
      }

.Alert--ios .Alert__btn:last-child::after,
    .Alert--ios .Alert__btn.Tappable--active::after {
      content: none;
      }

.Alert--ios.Alert--h .Alert__btn::after {
      top: 0;
      right: 0;
      width: 1px;
      height: 100%;
      }

.Alert--ios.Alert--h .Alert__btn {
      -webkit-box-flex: 1;
      -webkit-flex-grow: 1;
          -ms-flex-positive: 1;
              flex-grow: 1;
      -webkit-flex-shrink: 1;
          -ms-flex-negative: 1;
              flex-shrink: 1;
      -webkit-flex-basis: 0;
          -ms-flex-preferred-size: 0;
              flex-basis: 0;
      }

.Alert--ios.Alert--h .Alert__btn:first-child {
      border-bottom-left-radius: 12px;
      }

.Alert--ios.Alert--h .Alert__btn:last-child {
      border-bottom-right-radius: 12px;
      }

.Alert--ios.Alert--v .Alert__btn::after {
      left: 0;
      bottom: 0;
      width: 100%;
      height: 1px;
      }

.Alert--ios.Alert--v .Alert__btn:last-child {
      border-radius: 0 0 12px 12px;
      }

@media (-webkit-min-device-pixel-ratio: 2), (-o-min-device-pixel-ratio: 2/1), (min-resolution: 192dpi), (min-resolution: 2dppx) {
  .Alert--ios .Alert__content::after {
    background: -webkit-gradient(linear, left bottom, left top, color-stop(50%, rgba(0, 0, 0, .12)), color-stop(50%, transparent));
    background: -webkit-linear-gradient(bottom, rgba(0, 0, 0, .12) 50%, transparent 50%);
    background: -o-linear-gradient(bottom, rgba(0, 0, 0, .12) 50%, transparent 50%);
    background: linear-gradient(to top, rgba(0, 0, 0, .12) 50%, transparent 50%);
    }

    .Alert--ios.Alert--h .Alert__btn::after {
      background: -webkit-gradient(linear, right top, left top, color-stop(50%, rgba(0, 0, 0, .12)), color-stop(50%, transparent));
      background: -webkit-linear-gradient(right, rgba(0, 0, 0, .12) 50%, transparent 50%);
      background: -o-linear-gradient(right, rgba(0, 0, 0, .12) 50%, transparent 50%);
      background: linear-gradient(to left, rgba(0, 0, 0, .12) 50%, transparent 50%);
      }

    .Alert--ios.Alert--h .Alert__btn:last-child::after {
      content: none;
      }

    .Alert--ios.Alert--v .Alert__btn::after {
      background: -webkit-gradient(linear, left bottom, left top, color-stop(50%, rgba(0, 0, 0, .12)), color-stop(50%, transparent));
      background: -webkit-linear-gradient(bottom, rgba(0, 0, 0, .12) 50%, transparent 50%);
      background: -o-linear-gradient(bottom, rgba(0, 0, 0, .12) 50%, transparent 50%);
      background: linear-gradient(to top, rgba(0, 0, 0, .12) 50%, transparent 50%);
      }
  }

.Alert--ios .Alert__btn--primary { /* Depcrated. Remove in next version */
    font-weight: 500;
    }

.Alert--ios .Alert__btn--cancel {
    font-weight: 600;
    }

.Alert--ios .Alert__btn--destructive {
    color: #e64646;
    }

/**
 * Android version
 */

.Alert--android {
  min-width: 280px;
  margin: auto 24px;
  background: #fff;
  -webkit-box-shadow: 0 20px 35px 0 rgba(0, 0, 0, .3), 0 0 35px 0 rgba(0, 0, 0, .22);
          box-shadow: 0 20px 35px 0 rgba(0, 0, 0, .3), 0 0 35px 0 rgba(0, 0, 0, .22);
  border-radius: 2px;
  -webkit-animation: animation-android-alert-intro .2s ease;
          animation: animation-android-alert-intro .2s ease;
  }

/**
   * Content container
   */

.Alert--android .Alert__content {
    padding: 18px 24px 16px;
    font-size: 16px;
    line-height: 24px;
    color: #71757a;
    }

.Alert--android .Alert__content h2 {
      margin: 0 0 8px;
      font-weight: 500;
      font-size: 20px;
      line-height: 28px;
      color: #2e3033;
      }

.Alert--android .Alert__content p {
      margin: 0;
      }

.Alert--android .Alert__content p + p {
      margin: 24px 0 0;
      }

/**
   * Footer
   */

.Alert--android .Alert__footer {
    padding: 8px;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: end;
    -webkit-justify-content: flex-end;
        -ms-flex-pack: end;
            justify-content: flex-end;
    -webkit-flex-flow: wrap;
        -ms-flex-flow: wrap;
            flex-flow: wrap;
    }

.Alert--android .Alert__btn {
      background: transparent;
      margin: 0 0 0 12px;
      border: 0;
      -webkit-box-flex: 0;
      -webkit-flex: 0 1 auto;
          -ms-flex: 0 1 auto;
              flex: 0 1 auto;
      text-transform: uppercase;
      font-weight: 500;
      font-size: 14px;
      line-height: 16px;
      padding: 8px;
      border-radius: 2px;
      color: #5181b8;
      outline: none;
      overflow: hidden;
      -o-text-overflow: ellipsis;
         text-overflow: ellipsis;
      font-family: Roboto, Arial;
      }

.Alert--android .Alert__btn--cancel {
      color: #909499;
      }

.Alert--android .Alert__btn--primary {
      color: #fff;
      background-color: #5181b8;
      }

.Alert--android .Alert__btn--primary:active,
    .Alert--android .Alert__btn--primary.Tappable--active {
      background: #4872a3;
      }

