.Tappable {}

/**
 * iOS tappable
 */

.Tappable--ios {
  -webkit-transition: background-color .15s ease-out;
  -o-transition: background-color .15s ease-out;
  transition: background-color .15s ease-out;
  }

.Tappable--ios.Tappable--active:not([disabled]):not(.TabsItem):not(.HeaderButton):not(.Button--type-default):not(.PanelHeaderContent__in) {
  background: #d9d9d9 !important;
  -webkit-transition: none;
  -o-transition: none;
  transition: none;
  }

/**
 * Android tappable
 */

.Tappable--android {
  position: relative;
  -webkit-transition: background-color .15s ease-out;
  -o-transition: background-color .15s ease-out;
  transition: background-color .15s ease-out;
  }

.Tappable--android.Tappable--active:not([disabled]):not(.TabsItem):not(.HeaderButton):not(.Button--type-default):not(.PanelHeaderContent__in) {
  background: #f0f1f3 !important;
  }

/**
   * Waves container
   */

.Tappable--android .Tappable__waves {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    pointer-events: none;
    overflow: hidden;
    }

/**
     * Wave
     */

.Tappable--android .Tappable__wave {
      position: absolute;
      top: 0;
      left: 0;
      width: 24px;
      height: 24px;
      margin: -12px 0 0 -12px;
      opacity: 0;
      content: '';
      border-radius: 50%;
      background: rgba(127, 127, 127, .1);
      -webkit-animation: animation-wave .3s cubic-bezier(.4, 0, .2, 1);
              animation: animation-wave .3s cubic-bezier(.4, 0, .2, 1);
      }

/**
 * Animations
 */

@-webkit-keyframes animation-wave {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }

  30% {
    opacity: 1;
  }

  100% {
    -webkit-transform: scale(8);
            transform: scale(8);
    opacity: 0;
  }
}

@keyframes animation-wave {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }

  30% {
    opacity: 1;
  }

  100% {
    -webkit-transform: scale(8);
            transform: scale(8);
    opacity: 0;
  }
}
