/**
 * iOS
 */

.Group--ios {
  margin: 10px 0;
  background-color: #fff;
  position: relative;
  }

.Group--ios .Group__content .List::before,
  .Group--ios .Group__content .List::after {
    content: none;
    }

.Group--ios .Group__description {
    font-size: 14px;
    line-height: 16px;
    color: #71757a;
    padding: 8px 12px;
    }

.Group--ios::before,
  .Group--ios::after {
    position: absolute;
    right: 0;
    left: 0;
    z-index: 1;
    height: 1px;
    content: '';
    background: rgba(0, 0, 0, .12);
    }

.Group--ios::before {
    top: 0;
    }

.Group--ios::after {
    bottom: 0;
    }

@media (-webkit-min-device-pixel-ratio: 2), (-o-min-device-pixel-ratio: 2/1), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    .Group--ios::before {
      background: -webkit-gradient(linear, left top, left bottom, color-stop(50%, rgba(0, 0, 0, .12)), color-stop(50%, transparent));
      background: -webkit-linear-gradient(top, rgba(0, 0, 0, .12) 50%, transparent 50%);
      background: -o-linear-gradient(top, rgba(0, 0, 0, .12) 50%, transparent 50%);
      background: linear-gradient(to bottom, rgba(0, 0, 0, .12) 50%, transparent 50%);
      }

    .Group--ios::after {
      background: -webkit-gradient(linear, left bottom, left top, color-stop(50%, rgba(0, 0, 0, .12)), color-stop(50%, transparent));
      background: -webkit-linear-gradient(bottom, rgba(0, 0, 0, .12) 50%, transparent 50%);
      background: -o-linear-gradient(bottom, rgba(0, 0, 0, .12) 50%, transparent 50%);
      background: linear-gradient(to top, rgba(0, 0, 0, .12) 50%, transparent 50%);
      }
    }

/**
 * Android
 */

.Group--android {
  margin: 8px 0;
  background-color: #fff;
  -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, .06), 0 0 2px 0 rgba(0, 0, 0, .03);
          box-shadow: 0 2px 2px 0 rgba(0, 0, 0, .06), 0 0 2px 0 rgba(0, 0, 0, .03);
  }

.Group--android .Group__description {
    font-size: 14px;
    line-height: 16px;
    color: #909499;
    padding: 12px 16px 8px;
    }

