}

.Input {
  position: relative;
  line-height: 19px;
  font-size: 16px;
  }

.Input--center .Input__el {
  text-align: center;
  }

.Input--right .Input__el {
  text-align: right;
  }

.Input__el {
  margin: 0;
  width: 100%;
  display: block;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-box-shadow: none;
          box-shadow: none;
  border: none;
  outline: none;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  height: 44px;
  line-height: 19px;
  font-size: 16px;
  color: #000;
  padding: 12px;
  }

.Input__el:disabled {
  opacity: .6;
  }

.Input__el::-webkit-input-placeholder {
  color: #909499;
  }

.Input__el:-ms-input-placeholder {
  color: #909499;
  }

.Input__el::-ms-input-placeholder {
  color: #909499;
  }

.Input__el::placeholder {
  color: #909499;
  }

.Input__placeholder {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: calc(100% - 20px);
  color: #909499;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
  padding: 12px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  }

.Input--ios {
  font-family: -apple-system, Helvetica Neue, Arial;
  }

.Input--ios .Input__el {
    border-radius: 10px;
    background-color: rgb(242, 243, 245);
    border: solid .5px rgba(0, 0, 0, .15);
    }

.Input--ios .Input__el:focus {
    border-color: #528bcc;
    }

.Input--android {
  font-family: Roboto, Arial;
  }

.Input--android .Input__el {
    border-radius: 4px;
    background-color: #f2f3f5;
    }

.Input--android .Input__el:disabled ~ .Input-underline {
    opacity: .6;
    }

.Input--android .Input__el:focus ~ .Input-underline::before {
    -webkit-transform: scaleX(1);
        -ms-transform: scaleX(1);
            transform: scaleX(1);
    -webkit-transition: -webkit-transform .14s cubic-bezier(.4, 0, .2, 1);
    transition: -webkit-transform .14s cubic-bezier(.4, 0, .2, 1);
    -o-transition: transform .14s cubic-bezier(.4, 0, .2, 1);
    transition: transform .14s cubic-bezier(.4, 0, .2, 1);
    transition: transform .14s cubic-bezier(.4, 0, .2, 1), -webkit-transform .14s cubic-bezier(.4, 0, .2, 1);
    }
