html,
body,
div#root {
  margin: 0;
  padding: 0;
  height: 100%;
  height: 100vh;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: transparent;
  -webkit-text-size-adjust: 100%;
  font-family: -apple-system, Roboto, Helvetica Neue, Arial;
}

html {
  background: #ebedf0;
}

html[theme="white"] {
  background: #fff;
}

body {
  overflow-x: hidden;
}

#root {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  }

a:focus {
  outline: none;
}

.Input-underline,
.Select-underline,
.Textarea-underline {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 8px;
  border-radius: 4px;
  background-color: rgba(0, 0, 0, .12);
  z-index: 1;
}

.Textarea-underline {
  position: relative;
  }

.Input-underline::after,
.Select-underline::after,
.Textarea-underline::after {
  position: absolute;
  top: -1px;
  height: 7px;
  width: 100%;
  content: '';
  display: block;
  background-color: #f2f3f5;
  z-index: 2;
}

.Input-underline::before,
.Select-underline::before,
.Textarea-underline::before {
  position: absolute;
  height: 8px;
  width: 100%;
  bottom: 0;
  z-index: 1;
  border-radius: 4px;
  background: #528bcc;
  content: '';
  -webkit-transform: scaleX(0);
      -ms-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transition: -webkit-transform .1s cubic-bezier(.4, 0, .2, 1);
  transition: -webkit-transform .1s cubic-bezier(.4, 0, .2, 1);
  -o-transition: transform .1s cubic-bezier(.4, 0, .2, 1);
  transition: transform .1s cubic-bezier(.4, 0, .2, 1);
  transition: transform .1s cubic-bezier(.4, 0, .2, 1), -webkit-transform .1s cubic-bezier(.4, 0, .2, 1);
}
