.Checkbox {
  display: block;
  }

.Checkbox__input {
    display: none;
    }

.Checkbox__container {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: start;
    -webkit-align-items: flex-start;
        -ms-flex-align: start;
            align-items: flex-start;
    -webkit-box-pack: start;
    -webkit-justify-content: flex-start;
        -ms-flex-pack: start;
            justify-content: flex-start;
    }

.Checkbox__icon {
      -webkit-flex-shrink: 0;
          -ms-flex-negative: 0;
              flex-shrink: 0;
      width: 20px;
      height: 20px;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      margin: 2px 14px 2px 2px;
      border-radius: 4px;
      border: 2px solid #c4c8cc;
      color: transparent;
      }

.Checkbox__content {
      -webkit-box-flex: 1;
      -webkit-flex-grow: 1;
          -ms-flex-positive: 1;
              flex-grow: 1;
      font-size: 16px;
      line-height: 20px;
      margin: 2px 0;
      color: #000;
      }

.Checkbox__input:checked ~ .Checkbox__container .Checkbox__icon {
    background-color: #528bcc;
    border-color: #528bcc;
    color: #fff;
    }

.Checkbox__input[disabled] ~ .Checkbox__container {
    opacity: .6;
    }

.Checkbox--ios {
  padding: 12px;
  }

.Checkbox--ios .Checkbox__container {}

.Checkbox--ios .Checkbox__content {
      font-size: 16px;
      line-height: 20px;
      }

.Checkbox--android {
  padding: 16px;
  }

.Checkbox--android .Checkbox__container {}

.Checkbox--android .Checkbox__content {
      font-size: 16px;
      line-height: 20px;
      }
