.Search--ios {
  overflow: hidden;
  font-weight: normal;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  }

.Search--ios .Search__in {
    position: relative;
    }

.Search--ios .Search__width {
      height: 32px;
      white-space: nowrap;
      color: transparent;
      width: 10000px;
      }

.Search--ios .Search__control {
