.Gallery {
  position: relative;
  overflow: hidden;
  height: 100px;
  }

  .Gallery__viewport {
    margin: 0;
    height: 100%;
    position: relative;
    }

  .Gallery--center .Gallery__viewport {
    margin: 0 auto;
  }

  .Gallery__layer {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    height: 100%;
    cursor: -webkit-grab;
    cursor: grab;
    -webkit-box-align: stretch;
    -webkit-align-items: stretch;
        -ms-flex-align: stretch;
            align-items: stretch;
    }

  .Gallery__slide {
    width: 100%;
    height: 100%;
    -webkit-box-flex: 0;
    -webkit-flex: 0 0 auto;
        -ms-flex: 0 0 auto;
            flex: 0 0 auto;
    }

  .Gallery__slide > * {
      width: 100%;
      height: 100%;
      }

  .Gallery__bullets {
    position: absolute;
    bottom: 10px;
    height: 6px;
    width: 100%;
    left: 0;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
        -ms-flex-pack: center;
            justify-content: center;
    }

  .Gallery__bullets--dark .Gallery__bullet {
      background-color: #000;
      }

  .Gallery__bullets--light .Gallery__bullet {
      background-color: #fff;
      }

  .Gallery__bullet {
      height: 6px;
      width: 6px;
      border-radius: 50%;
      margin: 0 3px;
      opacity: .56;
      }

  .Gallery__bullet--active {
      opacity: 1;
      }
